.Comment-radiuo {
  width: 100%;
  display: block;
  background: #f6f7fb;
}
.Comment-radiuo .school-warp {
  width: 1100px;
  margin: 0 auto;
}
.Comment-radiuo .school-warpa {
  width: 1168px;
  margin: 0 auto;
}
.Comment-radiuo .ant-card-bordered {
  border: none;
  background: #f6f7fb;
}
.Comment-radiuo .ant-card-bordered .ant-card-body {
  background: none;
  padding: 24px 0 0 0;
}
.Comment-radiuo .paper-common {
  padding: 17px 0 0;
}
.Comment-radiuo .paper-common .common-col {
  margin-bottom: 23px;
}
.Comment-radiuo .paper-common .paper-year {
  color: #2A283D;
  font-weight: bold;
  display: inline-block;
  text-align: center;
  height: 38px;
  line-height: 38px;
}
.Comment-radiuo .paper-common .paper-common-btn .ant-btn {
  border: none;
  background: none;
  margin: 0 10px 5px 0;
  background: #EFEFEF;
  border-radius: 2px;
}
.Comment-radiuo .paper-common .paper-common-btn .ant-btn.ant-btn-primary {
  width: 110px;
  height: 32px;
  background: #3BB9FF !important;
  border-radius: 2px;
}
.Comment-radiuo .paper-common .paper-common-btn .ant-btn.ant-btn-primary span {
  color: #fff !important;
}
.Comment-radiuo .paper-common .paper-common-btn .ant-btn span {
  font-size: 14px;
  color: #000;
  font-weight: 400;
}
.Comment-radiuo .paper-common .ant-radio-button-wrapper {
  height: 38px;
  line-height: 38px;
  color: #000;
  background: rgba(210, 210, 210, 0.2);
  border-radius: 2px;
  border: none;
  width: 17.6%;
  margin-right: 3%;
  text-align: center;
  font-size: 20px;
  padding: 0;
}
.Comment-radiuo .paper-common .ant-radio-button-wrapper span {
  color: #0D1731;
  opacity: 0.8;
  font-size: 20px;
}
.Comment-radiuo .paper-common .ant-radio-button-wrapper::before {
  width: 0;
}
.Comment-radiuo .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  border-radius: 2px;
  height: 38px;
  line-height: 38px;
}
.Comment-radiuo .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked span {
  color: #fff !important;
  opacity: 1;
}
.Comment-radiuo .paper-common .ant-radio-button-wrapper:nth-child(5n) {
  margin-right: 0;
}
.Comment-radiuo .paper-common .actives {
  color: #2cbde6;
  border-color: #2cbde6;
}
.Comment-radiuo .paper-common .ant-divider-horizontal {
  margin: 16px 0;
}
.Comment-radiuo .paper-common .paper-common-search {
  text-align: right;
}
.Comment-radiuo .paper-common .paper-common-search .ant-input-affix-wrapper .ant-input {
  background: #F4F4F4;
  border-radius: 13.5px;
  height: 32px;
  opacity: 0.7;
}
.Comment-radiuo .paper-common .paper-common-search .paper-common-totale {
  margin-left: 20px;
  color: #000000;
  opacity: 0.3;
}
.Comment-radiuo .paper-commona .ant-radio-button-wrapper {
  margin-bottom: 20px;
}
